color: theme-color(success);\n  }\n\n  &.offline {\n    border-color: theme-color(primary);\n  }\n\n  &.away {\n    border-color: theme-color(warning);\n  }\n}\n\n.bg-transparent {\n  background: transparent;\n}\n\n.aligner-wrapper {\n  position: relative;\n\n  .absolute {\n    position: absolute;\n\n    &.absolute-center {\n      top: 50%;\n      transform: translateY(-50%);\n      bottom: 0;\n      right: 0;\n      left: 0;\n      margin: auto;\n    }\n\n    &.left {\n      left: 0;\n    }\n\n    &.right {\n      right: 0;\n    }\n\n    &.bottom {\n      bottom: 0;\n    }\n\n    &.top {\n      top: 0;\n    }\n  }\n}\n\n.v-strock-1 {\n  width: 2px;\n}\n\n.v-strock-2 {\n  width: 3px;\n}\n\n.v-strock-3 {\n  width: 4px;\n}\n\n.v-strock-4 {\n  width: 5px;\n}\n\n.h-strock-1 {\n  height: 2px;\n}\n\n.h-strock-2 {\n  height: 3px;\n}\n\n.h-strock-3 {\n  height: 4px;\n}\n\n.h-strock-4 {\n  height: 5px;\n}\n\n.list {\n  &.list-noborders {\n    .list-item {\n      border: none;\n    }\n  }\n\n  padding-left: 0;\n\n  .list-item {\n    display: flex;\n    align-items: center;\n    padding: 0 0 30px 0;\n\n    &:first-child {\n      padding-top: 0;\n    }\n\n    &:last-child {\n      padding-bottom: 0;\n    }\n  }\n}\n\n.text-transparent {\n  opacity: 0.6;\n}\n\n.text-avatar {\n  display: flex;\n  justify-content: center;\n  align-items: center;\n  font-weight: 500;\n}","/* Demo Styles */\n// Add spacing to Boostrap components for demo purpose\n.template-demo {\n  >.btn {\n    @extend .mt-2;\n    @extend .mr-2;\n  }\n\n  >.btn-toolbar {\n    @extend .mt-2;\n    @extend .mr-2;\n  }\n\n  >.btn-group {\n    @extend .mt-2;\n    @extend .mr-2;\n\n    .btn {\n      margin: 0 !important;\n    }\n  }\n\n  .progress {\n    margin-top: 1.5rem;\n  }\n\n  .circle-progress {\n    @extend .mt-2;\n  }\n\n  >h2,\n  >h3,\n  >h4,\n  >h5,\n  >h6,\n  >h1 {\n    border-top: 1px solid $border-color;\n    padding: 0.5rem 0 0;\n  }\n\n  .ul-slider {\n    &.noUi-horizontal {\n      margin-top: 2rem;\n    