r;\n    border-radius: 50px;\n\n    i {\n      font-size: 27px;\n    }\n  }\n\n  .card-col {\n    border-right: 1px solid $border-color;\n\n    &:last-child {\n      border-right: none;\n    }\n\n    @include media-breakpoint-down(sm) {\n      padding-left: 0;\n      padding-right: 0;\n      position: relative;\n      border: none;\n\n      &:first-child,\n      &:nth-child(2),\n      &:nth-child(3) {\n\n        &:after,\n        &:before {\n          content: \"\";\n          position: absolute;\n          background: $border-color;\n        }\n      }\n\n      &:first-child {\n        &:before {\n          bottom: 0;\n          width: 94.2%;\n          height: 1px;\n          right: 0;\n        }\n\n        &:after {\n          bottom: 0;\n          width: 1px;\n          height: 100%;\n          right: 0;\n        }\n      }\n\n      &:nth-child(2) {\n        &:before {\n          bottom: 0;\n          width: 94.2%;\n          height: 1px;\n          left: 0;\n        }\n      }\n\n      &:nth-child(3) {\n        &:before {\n          width: 1px;\n          height: 100%;\n          right: 0;\n        }\n      }\n    }\n  }\n}\n\n.card-revenue-table {\n  .revenue-item {\n    border-bottom: 1px solid $border-color;\n    @extend .py-3;\n\n    &:last-child {\n      border-bottom: 0;\n      @extend .pb-0;\n    }\n\n    &:first-child {\n      @extend .pt-0;\n    }\n\n    .revenue-desc {\n      margin-right: auto;\n      width: 80%;\n\n      p {\n        margin-bottom: 0;\n      }\n    }\n\n    .revenue-amount {\n      margin-left: auto;\n      width: 40%;\n\n      p {\n        font-size: 1.25rem;\n        font-family: $type-1;\n        font-weight: 600;\n        text-align: right;\n\n        .rtl & {\n          text-align: left;\n        }\n      }\n    }\n  }\n}\n\n.card-revenue {\n  background: linear-gradient(120deg, #556cdc, #128bfc, #18bef1);\n  background-size: cover;\n  color: color(white);\n\n  .highlight-text {\n    font-size: 1.875rem;\n    font-family: $type-1;\n    font-weight: 500;\n  }\n\n  .badge {\n    background-color: rgba(color(white), .2);\n    font-size: 1.125rem;\n    padding: 0.5rem 1.25rem;\n  }\n}\n\n.card-weather {\n  background: $card-bg;\n\n  .card-body {\n    background: $white;\n\n    &:first-child {\n      background: url(\"../../images/samples/weather.svg\") no-repeat center;\n      background-size: cover;\n    }\n  }\n\n  .weather-date-location {\n    padding: 0 0 38px;\n  }\n\n  .weather-data {\n    padding: 0 0 4.75rem;\n\n    i {\n      font-size: 5.313rem;\n      line-height: 1;\n    }\n  }\n\n  .weakly-weather {\n    background: $card-bg;\n    overflow-x: auto;\n\n    .weakly-weather-item {\n      flex: 0 0 14.28%;\n      border-right: 1px solid $border-color;\n      padding: 1rem;\n      text-align: center;\n\n      i {\n        font-size: 1.2rem;\n      }\n\n      &:last-child {\n        border-right: 0;\n      }\n\n      .symbol {\n        color: $text-muted;\n        font-size: 1.875rem;\n        font-weight: 300;\n      }\n    }\n  }\n}\n\n.product-chart-wrapper {\n  height: 92%;\n}\n\n#dashboardTrendingProgress {\n  width: 60px;\n}\n\n.dashboard-bar-chart-legend {\n  .col {\n    text-align: center;\n    display: flex;\n    align-items: center;\n    flex-direction: column;\n\n    .bg {\n      margin-left: auto;\n      margin-right: auto;\n      height: 5px;\n      width: 30px;\n      display: block;\n      margin-top: 5px;\n    }\n\n    &:nth-child(1) {\n      .bg {\n        background: theme-color(info);\n      }\n    }\n\n    &:nth-child(2) {\n      .bg {\n        background: theme-color(primary);\n      }\n    }\n\n    &:nth-child(3) {\n      .bg {\n        background: theme-color(danger);\n      }\n    }\n  }\n}\n\n.intro-banner {\n  background: #dbe4ec;\n  display: flex;\n  align-items: center;\n  padding: 20px;\n  border-radius: 2px;\n\n  .banner-image {\n    width: 24%;\n    max-width: 100%;\n\n    img {\n      display: block;\n      margin: auto;\n    }\n  }\n\n  .content-area {\n    width: 58%;\n    color: $black;\n    display: flex;\n    flex-direction: column;\n    align-items: end;\n  }\n}\n\n.sales-mini-tabs {\n  border-bottom: none;\n\n  .nav-item {\n    .nav-link {\n      border: none;\n      font-weight: 500;\n      padding: 5px 15px;\n      border-radius: 2px;\n      transition-duration: 0.7s;\n      transition-property: \"color\";\n\n      &.active {\n        font-weight: 600;\n        background: lighten($card-bg, 4%);\n        color: theme-color(primary);\n      }\n    }\n  }\n}","/* Bootstrap Progress */\n\n.progress {\n  border-radius: 3px;\n  height: 8px;\n  background: $progressbar-bg;\n\n  .progress-bar {\n    border-radius: 3px;\n  }\n\n  &.grouped {\n    .progress-bar {\n      border-radius: 0px;\n\n      &:last-child {\n        border-radius: 0px 3px 3px 0px;\n      }\n    }\n  }\n\n  &.progress-sm {\n    height: 0.375rem;\n  }\n\n  &.progress-md {\n    height: 8px;\n  }\n\n  &.progress-lg {\n    height: 15px;\n  }\n\n  &.progress-xl {\n    height: 18px;\n  }\n}","/* Buttons */\n\n.btn {\n  font-size: $btn-font-size;\n  line-height: 1;\n  font-family: $type-1;\n\n  i {\n    margin-right: 0.3125rem;\n    font-size: inherit;\n  }\n\n  .btn-label {\n    &:before {\n      font-size: 1rem;\n      line-height: 5px;\n      vertical-align: middle;\n    }\n\n    &.btn-label-left {\n      margin-right: 5px;\n    }\n\n    &.btn-label-right {\n      margin-left: 5px;\n    }\n  }\n\n  &.btn-rounded {\n    border-radius: 50px;\n  }\n\n  &.btn-square {\n    border-radius: 0px;\n  }\n\n  &.btn-icons {\n    width: 40px;\n    height: 40px;\n    padding: 10px;\n    text-align: center;\n    vertical-align: middle;\n\n    i {\n      margin: auto;\n      line-height: initial;\n    }\n  }\n\n  &.btn-fw {\n    min-width: $button-fixed-width;\n  }\n\n  &.icon-btn {\n    i {\n      margin-right: 0;\n    }\n  }\n\n  &.social-btn {\n    padding: $social-btn-padding;\n\n    i {\n      margin-right: 0;\n      font-size: $social-btn-icon-size;\n    }\n  }\n\n  &.btn-sm {\n    font-size: $btn-font-size-sm;\n  }\n\n  &.btn-lg {\n    font-size: $btn-font-size-lg;\n  }\n\n  &.btn-xs {\n    padding: $btn-padding-y-xs $btn-padding-x-xs;\n    font-size: $btn-font-size-xs;\n  }\n\n  &.btn-transparent {\n    background: transparent;\n  }\n\n  &.btn-danger,\n  &.btn-info,\n  &.btn-success,\n  &.btn-teal,\n  &.btn-warning {\n    color: $white;\n  }\n\n  &.btn-outline-light {\n    border-color: darken(theme-color(light), 15%);\n    color: darken(theme-color(light), 15%);\n  }\n\n  &.btn-outline-secondary {\n    color: rgba($black, 0.5);\n  }\n\n  &.btn-inverse-secondary {\n    background-color: rgba(theme-color(secondary), 0.5);\n    color: rgba($black, 0.5);\n\n    &:hover {\n      color: rgba($black, 0.5);\n    }\n  }\n\n  &.btn-inverse-light {\n    background-color: $white;\n    color: rgba($black, 0.5);\n    border-color: lighten($black, 85%);\n\n    &:hover {\n      color: rgba($black, 0.5);\n      border-color: lighten($black, 85%);\n    }\n  }\n}\n\n.btn-group {\n  border: $border-width solid $border-color;\n  border-radius: $btn-border-radius;\n\n  .btn {\n    border-top: none;\n    border-bottom: none;\n    border-left: none;\n\n    &:last-child {\n      border-right: none;\n    }\n\n    &.btn-primary {\n      border-color: darken(theme-color(primary), 3%);\n    }\n\n    &.btn-secondary {\n      border-color: darken(theme-color(secondary), 3%);\n    }\n\n    &.btn-info {\n      border-color: darken(theme-color(info), 3%);\n    }\n\n    &.btn-warning {\n      border-color: darken(theme-color(warning), 3%);\n    }\n\n    &.btn-success {\n      border-color: darken(theme-color(success), 3%);\n    }\n\n    &.btn-danger {\n      border-color: darken(theme-color(danger), 3%);\n    }\n\n    &.btn-dark {\n      border-color: darken(theme-color(dark), 3%);\n    }\n\n    &.btn-light {\n      border-color: darken(theme-color(light), 3%);\n    }\n  }\n}\n\n.btn-toolbar {\n  .btn-group {\n    +.btn-group {\n      @extend .ml-2;\n    }\n  }\n}\n\n/*social buttons*/\n\n@each $color,\n$value in $social-colors {\n  .btn-#{$color} {\n    @include social-button(social-color($color));\n  }\n}\n\n/*social buttons*/\n\n@each $color,\n$value in $social-colors {\n  .btn-social-outline-#{$color} {\n    @include btn-social-outline-variant(social-color($color));\n  }\n}\n\n/* inverse buttons */\n\n@each $color,\n$value in $theme-colors {\n  .btn-inverse-#{$color} {\n    @include button-inverse-variant($value);\n  }\n}\n\n/* Inverse Outlined Buttons */\n\n@each $color,\n$value in $theme-colors {\n  .btn-inverse-outline-#{$color} {\n    @include button-inverse-outline-variant($value);\n  }\n}","@mixin social-button($color) {\n\tbackground: $color;\n\tcolor: color(white);\n\n\t&:hover {\n\t\tbackground: darken($color, 10%);\n\t}\n\n\t&.btn-link {\n\t\tbackground: none;\n\t\tcolor: $color;\n\n\t\t&:hover {\n\t\t\tcolor: darken($color, 10%);\n\t\t}\n\t}\n}\n\n@mixin btn-social-outline-variant($color) {\n\tbackground: transparent;\n\tborder-color: $color;\n\tcolor: $color;\n\n\t&:hover {\n\t\tbackground: rgba($color, 0.2);\n\t}\n}\n\n@mixin button-inverse-variant($color, $color-hover: $white) {\n\tcolor: $color;\n\tbackground-color: rgba($color, 0.2);\n\tbackground-image: none;\n\tborder-color: rgba($color, 0);\n\n\t@include hover {\n\t\tcolor: $color-hover;\n\t\tbackground-color: $color;\n\t\tborder-color: $color;\n\t}\n\n\t&.disabled,\n\t&:disabled {\n\t\tcolor: $color;\n\t\tbackground-color: transparent;\n\t}\n\n\t&.active,\n\t&:active,\n\t.show>&.dropdown-toggle {\n\t\tcolor: $color-hover;\n\t\tbackground-color: $color;\n\t\tborder-color: $color;\n\t}\n}\n\n@mixin button-inverse-outline-variant($color, $color-hover: $white) {\n\tcolor: $color;\n\tbackground-image: none;\n\tbackground: transparent;\n\tborder-color: rgba($color, 0.2);\n\n\t@include hover {\n\t\tcolor: $color;\n\t\tbackground-color: rgba($color, 0.2);\n\t\tborder-color: rgba($color, 0.2);\n\t}\n\n\t&.focus,\n\t&:focus {\n\t\tbox-shadow: 0 0 0 3px rgba($color, .5);\n\t}\n\n\t&.disabled,\n\t&:disabled {\n\t\tcolor: $color;\n\t\tbackground-color: transparent;\n\t}\n\n\t&.active,\n\t&:active,\n\t.show>&.dropdown-toggle {\n\t\tcolor: $color-hover;\n\t\tborder-color: $color;\n\t}\n}","/* Cards */\n\n.card {\n  border: none;\n\n  .card-header {\n    background: lighten($card-bg, 3%);\n    padding: $card-padding-y $card-padding-x;\n    border-bottom: 1px solid $border-color;\n\n    h1,\n    h2,\n    h3,\n    h4,\n    h5,\n    h6,\n    p,\n    small {\n      margin-bottom: 0px;\n    }\n\n    &.header-sm {\n      padding: 15px $card-padding-x;\n      height: 60px;\n\n      p,\n      .dropdown .dropdown-toggle i {\n        color: $text-muted;\n      }\n    }\n  }\n\n  .card-body {\n    padding: $card-padding-y $card-padding-x;\n\n    &.no-gutter {\n      padding: 0;\n    }\n\n    +.card-body {\n      padding-top: 0;\n    }\n  }\n\n  &.card-rounded {\n    border-radius: 5px;\n  }\n\n  &.card-faded {\n    background: #b5b0b2;\n    border-color: #b5b0b2;\n  }\n\n  .card-subtitle {\n    color: rgba($body-color, 0.7);\n  }\n\n  &.card-no-shadow {\n    box-shadow: none;\n  }\n}","/* Checkboxes and Radios */\n\n.form-check,\n.form-radio {\n  position: relative;\n  display: block;\n  margin-top: 15px;\n  margin-bottom: 10px;\n\n  .form-check-label {\n    display: block;\n    padding-left: 30px;\n\n    line-height: 1.5;\n\n    input {\n      position: absolute;\n      margin-left: -20px;\n      margin-top: 4px\\9;\n      top: 0;\n      left: 0;\n\n      z-index: 1;\n      cursor: pointer;\n      opacity: 0;\n      filter: alpha(opacity=0);\n      margin-top: 0;\n    }\n  }\n}\n\n.form-check {\n  .form-check-label {\n    font-size: $default-font-size;\n    line-height: 1.5;\n    padding-left: 10px;\n\n    input {\n      &:checked {\n        +.input-helper {\n          &:before {\n            background-color: $form-check-label-input-helper-bg;\n          }\n\n          &:after {\n            width: 18px;\n            opacity: 1;\n            line-height: 18px;\n            transform: scale(1);\n          }\n        }\n      }\n\n      &:disabled {\n        +.input-helper {\n          &:before {\n            border-color: $border-color;\n          }\n        }\n\n        &:checked {\n          +.input-helper {\n            &:after {\n              color: $border-color;\n            }\n          }\n        }\n      }\n    }\n\n    .input-helper {\n      &:before {\n        position: absolute;\n        content: \"\";\n        top: 50%;\n        transform: translateY(-50%);\n        width: 18px;\n        height: 18px;\n        border-radius: 2px;\n        left: 0;\n        border: 2px solid $border-color;\n        transition: all;\n        transition-duration: 0s;\n        transition-duration: 250ms;\n      }\n\n      &:after {\n        transition: all;\n        transition-duration: 0s;\n        transition-duration: 250ms;\n        font-family: Material Design Icons;\n        opacity: 0;\n        filter: alpha(opacity=0);\n        transform: scale(0);\n        content: '\\F12C';\n        position: absolute;\n        font-size: 0.9375rem;\n        font-weight: bold;\n        left: 0;\n        top: 14%;\n        transform: translateY(-14%);\n        color: theme-color(info);\n      }\n    }\n  }\n\n  &.form-check-flat {\n    label {\n      input {\n        &:checked {\n          +.input-helper {\n            &:before {\n              background-color: theme-color(success);\n              border: none;\n            }\n          }\n        }\n\n        &:disabled {\n          +.input-helper {\n            &:after {\n              color: $form-check-label-input-helper-bg;\n            }\n\n            &:before {\n              border-color: $border-color;\n            }\n          }\n\n          &:checked {\n            +.input-helper {\n              &:before {\n                background: $form-check-label-input-helper-bg;\n              }\n            }\n          }\n        }\n      }\n\n      .input-helper {\n        &:before {\n          border: 2px solid $border-color;\n        }\n\n        &:after {\n          color: $form-check-label-input-helper-bg;\n        }\n      }\n    }\n  }\n}\n\n.form-radio {\n  label {\n    input {\n      +.input-helper {\n        &:before {\n          position: absolute;\n          content: \"\";\n          top: 50%;\n          transform: translateY(-50%);\n          left: 0;\n          border: 2px solid $border-color;\n          width: 20px;\n          height: 20px;\n          border-radius: 50%;\n          transition: all;\n          transition-duration: 0s;\n          -webkit-transition-duration: 250ms;\n          transition-duration: 250ms;\n        }\n\n        &:after {\n          content: \"\";\n          width: 8px;\n          height: 8px;\n          background: theme-color(danger);\n          border-radius: 50%;\n          top: 30%;\n          transform: translateY(-30%);\n          left: 6px;\n          transition: none;\n          transition-duration: 250s;\n          opacity: 0;\n          filter: alpha(opacity=0);\n          transform: scale(0);\n          position: absolute;\n        }\n      }\n\n      &:checked {\n        +.input-helper {\n          &:before {\n            background-color: $form-check-label-input-helper-bg;\n            border: 2px solid $border-color;\n            top: 30%;\n            transform: translateY(-30%);\n          }\n\n          &:after {\n            opacity: 1;\n            line-height: 1.5;\n            filter: alpha(opacity=100);\n            transform: scale(1);\n          }\n        }\n      }\n\n      &:disabled {\n        +.input-helper {\n          &:before {\n            border-color: $border-color;\n          }\n        }\n\n        &:checked {\n          +.input-helper {\n            &:before {\n              background-color: $form-check-label-input-helper-bg;\n              border-color: $border-color;\n            }\n\n            &:after {\n              background-color: $border-color;\n            }\n          }\n        }\n      }\n    }\n  }\n\n  &.form-radio-flat {\n    label {\n      input {\n        &:checked {\n          +.input-helper {\n            &:before {\n              background: theme-color(success);\n              border-color: theme-color(success);\n              top: 50%;\n              transform: translateY(-50%);\n            }\n\n            &:after {\n              width: 20px;\n              height: 20px;\n              top: 50%;\n              transform: translateY(-50%);\n              left: -2px;\n              color: $form-check-label-input-helper-bg;\n              background: none;\n              content: '\\F12C';\n              font-family: Material Design Icons;\n              text-align: center;\n              font-weight: bold;\n            }\n          }\n        }\n\n        &:disabled {\n          +.input-helper {\n            &:before {\n              background: rgba(theme-color(success), 0.4);\n              border-color: rgba(theme-color(success), 0.05);\n            }\n          }\n        }\n      }\n    }\n  }\n}","/* Dropdowns */\n\n.dropdown {\n  .dropdown-toggle {\n    display: flex;\n    align-items: center;\n\n    &:after {\n      border: none;\n      content: \"\\F140\";\n      font-family: \"Material Design Icons\";\n      font-size: 20px;\n      color: $dropdown-toggle-arrow-color;\n      text-rendering: auto;\n      line-height: inherit;\n      vertical-align: 0;\n    }\n\n    &.arrow-disabled {\n      border: none;\n      outline: none;\n      box-shadow: none;\n\n      &:focus,\n      &:visited,\n      &:active {\n        outline: none;\n        box-shadow: none;\n      }\n\n      &:after {\n        display: none;\n      }\n    }\n  }\n}\n\n.dropdown-menu {\n  font-size: $default-font-size;\n  border: 1px solid $border-color;\n\n  @media (max-width: 992px) {\n    position: unset;\n  }\n\n  .dropdown-header {\n    background: $dropdown-bg;\n    color: $body-color;\n\n    * {\n      background: inherit;\n      color: inherit;\n    }\n  }\n\n  a:not([href]):not([tabindex]):hover,\n  a:not([href]):not([tabindex]):focus {\n    &.dropdown-item {\n      background: $dropdown-bg;\n      color: $body-color;\n\n      &:hover {\n        background: $dropdown-hover-bg;\n        color: $dropdown-hover-color;\n      }\n\n      &:active {\n        background: initial;\n      }\n    }\n  }\n\n  .dropdown-item {\n    background: $dropdown-bg;\n    color: $body-color;\n\n    &:hover {\n      background: $dropdown-hover-bg;\n      color: $dropdown-hover-color;\n    }\n\n    &:active {\n      background: initial;\n    }\n  }\n\n  &.dropdown-menu-left {\n    left: 0 !important;\n    right: auto !important;\n\n    @media (max-width: 991px) {\n      right: 0 !important;\n    }\n  }\n\n  &.dropdown-menu-right {\n    left: auto !important;\n    right: 0 !important;\n\n    @media (max-width: 991px) {\n      left: 0 !important;\n    }\n  }\n}","/* Forms */\n\n.input-group-append,\n.input-group-prepend {\n  background: $input-bg;\n  color: $input-placeholder-color;\n  width: auto;\n  border: none;\n\n  .input-group-text {\n    background: transparent;\n    border-color: $border-color;\n  }\n}\n\n.form-control {\n  display: inline-block;\n  border: 1px solid $border-color;\n  font-family: $type-1;\n  font-size: $input-font-size;\n  color: $body-color;\n  padding: 0 .75rem;\n  line-height: 14px;\n  font-weight: 300;\n\n  &.form-control-lg {\n    padding: $input-btn-padding-y-lg .75rem;\n  }\n\n  &.form-control-sm {\n    padding: $input-btn-padding-y-sm .75rem;\n  }\n}\n\ntextarea.form-control {\n  height: auto;\n  padding-top: 1rem;\n  padding-bottom: 1rem;\n}\n\nselect {\n  &.form-control {\n    padding: 0.4375rem 0.75rem;\n  }\n}\n\n.form-group {\n  label {\n    font-size: $default-font-size;\n    line-height: 1;\n    vertical-align: top;\n  }\n\n  &.has-danger {\n    .form-control {\n      border-color: theme-color(danger);\n    }\n  }\n\n  .file-upload-default {\n    visibility: hidden;\n    position: absolute;\n  }\n\n  .file-upload-info {\n    background: transparent;\n  }\n}","/* Icons */\n\n.icons-list {\n  border-left: 1px solid $border-color;\n  border-bottom: 1px solid $border-color;\n\n  >div {\n    background: $card-bg;\n    border-top: 1px solid $border-color;\n    border-right: 1px solid $border-color;\n    display: flex;\n    align-items: center;\n    padding: 15px;\n    font-family: $type-1;\n    font-size: $default-font-size;\n\n    i {\n      display: inline-block;\n      font-size: 20px;\n      width: 40px;\n      text-align: left;\n      color: theme-color(primary);\n    }\n  }\n}","/* Lists */\n\ndl,\nol,\nul {\n  padding-left: 1rem;\n  font-size: $default-font-size;\n\n  li {\n    line-height: 1.8;\n  }\n}\n\n.list-arrow,\n.list-star,\n.list-ticked {\n  list-style: none;\n  padding: 0;\n\n  li {\n    padding-left: 1.5rem;\n\n    &:before {\n      font-family: \"Material Design Icons\";\n      margin-left: -1.5rem;\n      width: 1.5rem;\n      margin-right: 0.5rem;\n    }\n  }\n}\n\n.list-ticked {\n  li {\n    &:before {\n      content: '\\F12D';\n      color: theme-color(danger);\n    }\n  }\n}\n\n.list-arrow {\n  li {\n    &:before {\n      content: '\\F142';\n      color: theme-color(success);\n    }\n  }\n}\n\n.list-star {\n  li {\n    &:before {\n      content: '\\F4CE';\n      color: theme-color(warning);\n    }\n  }\n}\n\n.bullet-line-list {\n  padding-left: 30px;\n  margin-bottom: 0;\n  position: relative;\n  list-style-type: none;\n\n  li {\n    position: relative;\n    line-height: 1;\n    padding-bottom: 10px;\n\n    &:before {\n      content: \"\";\n      position: absolute;\n      border-radius: 100%;\n      width: 12px;\n      height: 12px;\n      left: -28px;\n      top: 6px;\n      border: 3px solid theme-color(primary);\n      margin-right: 15px;\n      z-index: 2;\n      background: $bullet-line-list-shape-bg;\n    }\n\n    &:after {\n      content: \"\";\n      border: 1px solid $border-color;\n      position: absolute;\n      bottom: 0;\n      left: -23px;\n      height: 100%;\n    }\n\n    &:first-child {\n      &:after {\n        content: \"\";\n        height: 80%;\n      }\n    }\n\n    &:last-child {\n      padding-bottom: 0;\n\n      &:after {\n        content: \"\";\n        top: 0;\n        height: 30%;\n      }\n    }\n  }\n}","/* Preview */\n\n.preview-list {\n  .preview-item {\n    display: flex;\n    flex-direction: row;\n    align-items: flex-start;\n    padding: 0.75rem 1.5rem;\n    font-size: 0.875rem;\n    border-bottom: 1px solid $border-color;\n\n    &:last-child {\n      border-bottom: 0;\n    }\n\n    .form-check {\n      margin-top: 8px;\n      margin-right: 1rem;\n    }\n\n    .preview-thumbnail {\n      color: color(white);\n      position: relative;\n\n      .preview-icon,\n      img {\n        border-radius: 100%;\n      }\n\n      .preview-icon {\n        padding: 6px;\n        text-align: center;\n\n        i {\n          font-size: 1.125rem;\n        }\n      }\n\n      .badge {\n        border: 2px solid $border-color;\n        border-radius: 100%;\n        bottom: 5px;\n        display: block;\n        height: 14px;\n        left: -5px;\n        padding: 0;\n        position: absolute;\n        width: 14px;\n\n        &.badge-online {\n          @extend .badge-success;\n        }\n\n        &.badge-offline {\n          @extend .badge-info;\n        }\n\n        &.badge-busy {\n          @extend .badge-warning;\n        }\n      }\n    }\n\n    .preview-item-content {\n      line-height: 1;\n      padding-left: 1.5rem;\n      color: $body-color;\n\n      &:first-child {\n        padding-left: 0;\n      }\n\n      p {\n        margin-bottom: 10px;\n\n        .content-category {\n          font-family: $type-1;\n          padding-right: 15px;\n          border-right: 1px solid $border-color;\n          @extend .text-muted;\n        }\n      }\n    }\n\n    .preview-actions {\n      display: flex;\n      flex-direction: row;\n\n      i {\n        width: 29px;\n        color: color(gray-lightest);\n        height: 29px;\n        border: 2px solid color(gray-lightest);\n        border-radius: 100%;\n        padding: 3px 6px;\n        display: inline-block;\n\n        &:first-child {\n          margin-right: 10px;\n        }\n      }\n    }\n  }\n\n  &.comment-preview {\n    .preview-item {\n      padding: 0.87rem 0;\n\n      &:first-child {\n        padding-top: 0;\n      }\n\n      p {\n        line-height: 27px;\n      }\n    }\n  }\n\n  &.bordered {\n    .preview-item {\n      border-bottom: 1px solid $border-color;\n\n      &:last-child {\n        border-bottom: 0;\n      }\n    }\n  }\n}","/* Tables */\n\n.table {\n  margin-bottom: 0;\n\n  thead {\n    th {\n      border-top: 0;\n      border-bottom-width: 1px;\n      font-family: $type-2;\n      font-weight: 500;\n      color: $table-head-cell-color;\n      border-bottom-color: $border-color;\n\n      i {\n        margin-left: 0.325rem;\n      }\n    }\n  }\n\n  tbody {\n    td {\n      border-top-color: $border-color;\n    }\n  }\n\n  td,\n  th {\n    vertical-align: middle;\n    font-size: $default-font-size;\n    line-height: 1;\n    white-space: nowrap;\n    height: 35px;\n    padding: $table-cell-padding;\n\n    img {\n      &:not(.thumb-image) {\n        @extend .img-ss;\n        border-radius: 100%;\n      }\n\n      &.thumb-image {\n        width: 50px;\n        height: 50px;\n        border: 1px solid $border-color;\n      }\n    }\n\n    .badge {\n      margin-bottom: 0;\n    }\n\n    .form-check,\n    .form-radio {\n      margin-top: 0;\n      margin-bottom: -0px;\n    }\n  }\n\n  td {\n    color: $table-color;\n\n    .form-check {\n      .form-check-label {\n        input:checked+.input-helper {\n          &:after {\n            line-height: 1px;\n          }\n        }\n      }\n    }\n  }\n\n  &.table-borderless {\n    border: none;\n\n    td,\n    th,\n    tr {\n      border: none;\n    }\n  }\n\n  &.table-bordered {\n    thead {\n      border: 1px solid $border-color;\n      border-bottom: none;\n\n      tr {\n        &:not(.enable-border) {\n          th {\n            border-left: none;\n            border-right: none;\n          }\n        }\n      }\n    }\n\n    td {\n      border-color: $border-color;\n    }\n\n    &.table-edge-border-0 {\n      border-left: none;\n      border-right: none;\n      border-bottom: none;\n\n      thead {\n        border-left: none;\n        border-right: none;\n      }\n\n      th,\n      td {\n        &:first-child {\n          border-left: 1px solid transparent;\n        }\n\n        &:last-child {\n          border-right: 1px solid transparent;\n        }\n      }\n    }\n  }\n\n  &.table-striped {\n    tbody {\n      tr {\n        &:nth-child(odd) {\n          background: $table-accent-bg;\n        }\n\n        &:nth-child(even) {\n          background: transparent;\n        }\n      }\n    }\n  }\n\n  &.table-stretched {\n\n    thead,\n    tbody {\n      tr {\n\n        th,\n        td {\n          &:first-child {\n            padding-left: 0;\n          }\n\n          &:last-child {\n            padding-right: 0;\n            text-align: right;\n          }\n        }\n      }\n    }\n  }\n}",".timeline {\n    list-style: none;\n    padding-left: 0;\n\n    .timeline-item {\n        border-left: 1px solid $border-color;\n        color: $text-muted;\n        padding-left: 20px;\n        padding-bottom: 20px;\n        position: relative;\n\n        &:before {\n            content: \"\";\n            height: 10px;\n            width: 10px;\n            border-radius: 10px;\n            position: absolute;\n            top: 0px;\n            left: -5px;\n        }\n\n        &:nth-child(4n + 1) {\n            &:before {\n                background: theme-color(success);\n            }\n        }\n\n        &:nth-child(4n + 2) {\n            &:before {\n                background: theme-color(primary);\n            }\n        }\n\n        &:nth-child(4n + 3) {\n            &:before {\n                background: theme-color(warning);\n            }\n        }\n\n        &:nth-child(4n + 4) {\n            &:before {\n                background: theme-color(info);\n            }\n        }\n\n        &:last-child {\n            border-left: none;\n            padding-bottom: 0;\n        }\n\n        .timeline-content {\n            margin-bottom: 0;\n            line-height: 10px;\n\n        }\n\n        .event-time {\n            margin-bottom: 0;\n        }\n    }\n}","#chartjs-tooltip {\n  background: lighten($card-bg, 4%);\n  border-radius: 4px;\n  padding: 10px;\n  box-shadow: $chartjs-tooltip-box-shadow;\n  transition: 0.3s ease;\n\n  table {\n    tr {\n      th {\n        color: $text-muted;\n      }\n\n      td {\n        display: flex;\n        align-items: center;\n        line-height: 1px;\n        padding-top: 5px;\n        padding-bottom: 5px;\n\n        &:first-letter {\n          text-transform: uppercase;\n        }\n\n        span {\n          width: 7px;\n          height: 7px;\n          display: inline-block;\n          border-radius: 100px;\n          margin-right: 7px;\n          color: $body-color;\n        }\n      }\n    }\n  }\n}\n\n.chartjs-legend {\n  ul {\n    list-style: none;\n    padding-left: 0;\n    display: flex;\n    align-items: center;\n    margin-bottom: 0;\n\n    li {\n      margin-right: 8%;\n      display: flex;\n      align-items: center;\n      white-space: nowrap;\n      color: $text-muted;\n\n      span {\n        height: 10px;\n        width: 10px;\n        border-radius: 100%;\n        margin-right: 10px;\n        display: inline-block;\n        font-size: $default-font-size;\n      }\n    }\n  }\n\n  .rtl & {\n    ul {\n      padding-right: 0;\n\n      li {\n        margin-right: 0;\n        margin-left: 8%;\n\n        span {\n          margin-right: 0;\n          margin-left: 1rem;\n        }\n      }\n    }\n  }\n}",".item-purchase-