ext {\n              border-radius: 0 6px 6px 0;\n              border-left: none;\n              border-color: darken($border-color, 5%);\n              color: #b6b6b6;\n            }\n          }\n        }\n\n        .submit-btn {\n          font-family: $type-1;\n          font-size: 13px;\n          padding: 12px 8px;\n          font-weight: 600;\n        }\n      }\n\n      .g-login {\n        border: 1px solid $border-color;\n        padding: 13px;\n        font-size: 12px;\n        font-weight: 600;\n        background: transparent;\n      }\n    }\n\n    .auth-footer {\n      list-style-type: none;\n      padding-left: 0;\n      margin-top: 20px;\n      margin-bottom: 10px;\n      display: flex;\n      justify-content: center;\n\n      li {\n        margin-right: 10px;\n        line-height: 1;\n        padding-right: 10px;\n        border-right: 1px solid rgba(255, 255, 255, 0.4);\n\n        &:last-child {\n          margin-right: 0;\n          border-right: none;\n        }\n\n        a {\n          font-size: 13px;\n          color: rgba(255, 255, 255, 0.4);\n        }\n      }\n\n      @media (max-width: $grid-breakpoints-sm) {\n        justify-content: center;\n      }\n    }\n\n    .footer-text {\n      color: rgba(255, 255, 255, 0.4);\n    }\n  }\n}","/* Error */\n.error-page {\n\tmin-height: 100vh;\n\n\th1 {\n\t\tfont-size: 9.375rem;\n\t\tline-height: 1;\n\n\t\t@media (max-width: 991px) {\n\t\t\tfont-size: 8rem;\n\t\t}\n\t}\n\n\th2 {\n\t\tfont-size: 4.375rem;\n\t\tline-height: 1;\n\t}\n\n\t.error-page-divider {\n\t\t@media (min-width: 992px) {\n\t\t\tborder-left: 3px solid rgba($white, .2);\n\t\t}\n\t}\n}"],"sourceRoot":""}]);
