AEQ,kBAAgB,EAAA;AAChB;IAHR,cAAA,EAAA;AAOgB;MAPhB,WAAA;MAUoB,WAAA;MACA,kBAAW,EAAA;ANixB/B;EMzwBA,gBAAA;EAGY,cAAA,EAAA;AN0wBZ;EMpwBA,oBAAA;ANuwBA;EMvwBA,gBAAA,EAAA;AN0wBA;EM1wBA,kBAAA;EAUQ,yBAAkB;EAClB,WAAA;EACA,YAAW;EAEX,aAAA;EACA,mBAAA;EACA,uBAAuB;EAAvB,gBAAA;EACA,WAAA;EACA,eRjCuB,EAAA;AQkCvB;IAnBR,cAAA,EAAA;AN8xBA;EMnwBA,aAAA;EACI,WAAA,EAAA;AACA;;INswBA,aAAa,EAAE;AAEnB,oCAAoC","sourcesContent":["/* Layouts */\n\n@import \"../../../../node_modules/bootstrap/scss/functions\";\n@import \"../../../../node_modules/bootstrap/scss/variables\";\n@import \"../shared/variables\";\n@import \"./variables\";\n@import \"../shared/mixins/misc\";\n@import \"../shared/mixins/animation\";\n@import \"./nav\";\n@import \"./navbar\";\n@import \"./sidebar\";\n@import \"./footer\";\n@import \"./layouts\";\n@import \"./card\";\n@import \"./misc\";\n@import \"./dashboard\";","/* Animation Mixins */\n\n@keyframes dropdownAnimation {\n  from {\n    opacity: 0;\n    transform: translate3d(0, -30px, 0);\n  }\n\n  to {\n    opacity: 1;\n    transform: none;\n    transform: translate3d(0, 0px, 0);\n  }\n}\n\n.dropdownAnimation {\n  animation-name: dropdownAnimation;\n  animation-duration: $action-transition-duration;\n  animation-fill-mode: both;\n}\n\n@mixin transition($settings) {\n  -webkit-transition: $settings;\n  -moz-transition: $settings;\n  -ms-transition: $settings;\n  -o-transition: $settings;\n  transition: $settings;\n}\n\n@keyframes fadeOut {\n  from {\n    opacity: 1;\n  }\n\n  to {\n    opacity: 0;\n  }\n}\n\n.fadeOut {\n  animation-name: fadeOut;\n}\n\n.infinite-spin {\n  @keyframes spin {\n    from {\n      transform: rotate(0deg);\n    }\n\n    to {\n      transform: rotate(360deg);\n    }\n  }\n\n  animation-name: spin;\n  animation-duration: 3s;\n  animation-iteration-count: infinite;\n  animation-timing-function: linear;\n}\n\n@keyframes fadeInUp {\n  from {\n    opacity: 0;\n    transform: translate3d(0, 100%, 0);\n  }\n\n  to {\n    opacity: 1;\n    transform: none;\n  }\n}\n\n.fadeInUp {\n  animation-name: fadeInUp;\n}\n\n@keyframes menufadeInLeft {\n  from {\n    opacity: 0;\n    -webkit-transform: translate3d(-20px, 0, 0);\n    transform: translate3d(-20px, 0, 0);\n  }\n\n  to {\n    opacity: 1;\n    -webkit-transform: translate3d(0, 0, 0);\n    transform: translate3d(0, 0, 0);\n  }\n}\n\n.menufadeInLeft {\n  -webkit-animation-name: menufadeInLeft;\n  animation-name: menufadeInLeft;\n}",".nav,\n.navbar-nav {\n  .nav-item {\n    line-height: 1;\n\n    &.dropdown {\n      @media (max-width: 992px) {\n        position: unset;\n      }\n\n      .dropdown-toggle {\n        &:after {\n          border: none;\n          content: \"\\F140\";\n          font-family: \"Material Design Icons\";\n          font-size: 20px;\n          color: $navbar-dropdown-toggle-arrow-color;\n          text-rendering: auto;\n          line-height: inherit;\n          vertical-align: 0;\n        }\n      }\n\n      .count-indicator {\n        position: relative;\n        text-align: center;\n\n        i {\n          font-size: 21px;\n          margin-right: 0;\n          vertical-align: middle;\n        }\n\n        .count {\n          position: absolute;\n          left: 50%;\n          width: 1rem;\n          height: 1rem;\n          border-radius: 100%;\n          background: $navbar-count-indicator-bg;\n          color: $white;\n          font-size: 11px;\n          top: -1px;\n          font-weight: 600;\n          line-height: 1rem;\n          border: none;\n          text-align: center;\n        }\n\n        &:after {\n          display: none;\n        }\n      }\n\n      i {\n        margin-right: 0.5rem;\n        vertical-align: middle;\n\n        .rtl & {\n          margin-left: 0.5rem;\n          margin-right: 0;\n        }\n      }\n\n      .navbar-dropdown {\n        font-size: 0.9rem;\n        margin-top: 15px;\n        position: absolute;\n        right: 0;\n        left: auto;\n        border: 1px solid $navbar-dropdown-border-color;\n        padding: 0 0 20px;\n        min-width: 100%;\n        border-radius: 2px;\n        @extend .dropdownAnimation;\n        box-shadow: $navbar-dropdown-box-shadow;\n        overflow-x: hidden;\n        max-height: 350px;\n        overflow-y: auto;\n\n        .rtl & {\n          right: auto;\n          left: 0;\n        }\n\n        overflow: hidden;\n\n        @media (max-width: 991px) {\n          right: -85px;\n        }\n\n        &.dropdown-left {\n          left: 0;\n          right: auto;\n        }\n\n        .badge {\n          margin-left: 0.5rem;\n\n          .rtl & {\n            margin-left: 0;\n            margin-right: 1.25rem;\n          }\n\n          @media (max-width:991px) {\n            margin-right: 0.5rem;\n\n            .rtl & {\n              margin-left: 0.5rem;\n              margin-right: 0;\n            }\n          }\n        }\n\n        .dropdown-item {\n          display: flex;\n          align-items: center;\n          margin-bottom: 0;\n          padding: 5px 25px;\n\n          i {\n            font-size: 24px;\n          }\n\n          .ellipsis {\n            max-width: 200px;\n            overflow: hidden;\n            text-overflow: ellipsis;\n          }\n\n          .preview-icon {\n            width: 40px;\n            height: 40px;\n            display: flex;\n            align-items: center;\n            justify-content: center;\n          }\n\n          .small-text {\n            font-size: 0.75rem;\n          }\n        }\n\n        .dropdown-divider {\n          margin: 0;\n        }\n      }\n    }\n  }\n}","/* Navbar */\n\n.navbar {\n  &.default-layout {\n    font-family: $type-2;\n    background: $navbar-bg;\n    transition: background $action-transition-duration $action-transition-timing-function;\n    -webkit-transition: background $action-transition-duration $action-transition-timing-function;\n    -moz-transition: background $action-transition-duration $action-transition-timing-function;\n    -ms-transition: background $action-transition-duration $action-transition-timing-function;\n\n    .navbar-brand-wrapper {\n      transition: width $action-transition-duration $action-transition-timing-function, background $action-transition-duration $action-transition-timing-function;\n      -webkit-transition: width $action-transition-duration $action-transition-timing-function, background $action-transition-duration $action-transition-timing-function;\n      -moz-transition: width $action-transition-duration $action-transition-timing-function, background $action-transition-duration $action-transition-timing-function;\n      -ms-transition: width $action-transition-duration $action-transition-timing-function, background $action-transition-duration $action-transition-timing-function;\n      background: $sidebar-gradient-color-2;\n      width: $sidebar-width-lg;\n      height: $navbar-height;\n\n      .navbar-brand {\n        color: $white;\n        font-size: 1.5rem;\n        line-height: 48px;\n        margin-right: 0;\n        padding: 0.25rem 0;\n        display: flex;\n\n        &:active,\n        &:focus,\n        &:hover {\n          color: lighten(color(gray-dark), 10%);\n        }\n\n        img {\n          width: 170px;\n          max-width: 100%;\n          margin: auto;\n          vertical-align: middle;\n        }\n      }\n\n      .brand-logo-mini {\n        display: none;\n\n        img {\n          width: 40px;\n          max-width: 100%;\n          margin: auto;\n        }\n      }\n    }\n\n    .navbar-menu-wrapper {\n      transition: width $action-transition-duration $action-transition-timing-function;\n      -webkit-transition: width $action-transition-duration $action-transition-timing-function;\n      -moz-transition: width $action-transition-duration $action-transition-timing-function;\n      -ms-transition: width $action-transition-duration $action-transition-timing-function;\n      color: $navbar-menu-wrapper-color;\n      padding-left: 15px;\n      padding-right: 15px;\n      width: calc(100% - #{$sidebar-width-lg});\n      height: $navbar-height;\n      box-shadow: $navbar-menu-wrapper-box-shadow;\n\n      @media (max-width: 991px) {\n        width: calc(100% - #{$sidebar-width-icon});\n      }\n\n      .navbar-toggler {\n        border: 0;\n        color: inherit;\n\n        &:not(.navbar-toggler-right) {\n          @media (max-width: 991px) {\n            display: none;\n          }\n        }\n      }\n\n      .search-form {\n        width: 35%;\n        margin-right: 30px;\n\n        .form-group {\n          margin-bottom: 0;\n        }\n      }\n\n      .navbar-nav {\n        flex-direction: row;\n        align-items: center;\n\n        .nav-item {\n          margin-left: 1rem;\n\n          @media (max-width: 992px) {\n            margin-left: 10px;\n            margin-right: 0px;\n          }\n\n          .nav-link {\n            color: inherit;\n            font-size: $navbar-font-size;\n            vertical-align: middle;\n\n            @media (max-width: 767px) {\n              margin-left: 0.5rem;\n              margin-right: 0.5rem;\n            }\n\n            i {\n              font-size: $navbar-icon-font-size;\n              vertical-align: middle;\n              margin-right: 0;\n            }\n          }\n\n          &.user-dropdown {\n            margin-left: 35px;\n\n            &:before {\n              position: relative;\n              top: 2px;\n              font-size: 20px;\n              color: $navbar-dropdown-toggle-arrow-color;\n            }\n\n            .dropdown-menu {\n              min-width: 250px;\n              margin-top: 9px;\n\n              .dropdown-header {\n                padding: 20px;\n              }\n\n              .dropdown-item {\n                padding: 10px 15px;\n              }\n            }\n\n            .dropdown-toggle {\n              &:after {\n                position: relative;\n                top: 50%;\n              }\n            }\n          }\n\n          &.language-dropdown {\n            .dropdown-menu {\n              width: 170px;\n              left: -15px !important;\n\n\n              .dropdown-item {\n                padding-left: 22px;\n                padding-right: 12px;\n\n                .flag-icon-holder {\n                  margin-right: 15px;\n                }\n              }\n            }\n\n            .flag-icon-holder {\n              height: 25px;\n              width: 25px;\n              border-radius: 100%;\n              overflow: hidden;\n\n              i {\n                font-size: 25px;\n              }\n            }\n          }\n        }\n\n        &.navbar-nav-right {\n          @media (min-width: 992px) {\n            margin-left: auto;\n          }\n        }\n      }\n    }\n  }\n}\n\n@media (max-width:991px) {\n  .navbar {\n    &.default-layout {\n      flex-direction: row;\n\n      .navbar-brand-wrapper {\n        width: $sidebar-width-icon;\n\n        .brand-logo {\n          display: none;\n        }\n\n        .brand-logo-mini {\n          display: inline-block;\n        }\n      }\n    }\n  }\n\n  .navbar-collapse {\n    display: flex;\n    margin-top: 0.5rem;\n  }\n}","////////// COLOR SYSTEM //////////\n$blue: #00aeef;\n$indigo: #6610f2;\n$purple: #ab8ce4;\n$pink: #E91E63;\n$red: #ff0017;\n$orange: #fb9678;\n$yellow: #ffd500;\n$green: #3bd949;\n$teal: #58d8a3;\n$cyan: #57c7d4;\n$black: #000;\n$white: #ffffff;\n$white-smoke: #f4f4f4;\n$ghost-white: #f7fafc;\n$violet: #41478a;\n$darkslategray: #2e383e;\n$dodger-blue: #3498db;\n$blue-teal-gradient: linear-gradient(120deg, #556cdc, #128bfc, #18bef1);\n$blue-teal-gradient-light: linear-gradient(120deg, rgba(0, 228, 208, 0.7), rgba(89, 131, 232, 0.7));\n$theme-colors: (primary: #2196f3, secondary: #dde4eb, success:#19d895, info: #8862e0, warning: #ffaf00, danger: #ff6258, light: #fbfbfb, dark: #252C46);\n$colors: (blue: $blue, indigo: $indigo, purple: $purple, pink: $pink, red: $red, orange: $orange, yellow: $yell